*{
  margin: 0px;
  padding: 0px;
  outline: none;
}
.con{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap:20px;
  padding: 20px 0px;
  background-color: burlywood;
}
.prod{
  width: 35%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap:5px;
  background-color: cadetblue;
  border: 2px outset gray;
}
.img{
  width: 70px;
  height: 70px;
}
.img img{
  width: 100%;
  height: 100%;
}
p{
  font-size: 15px;
  text-align: justify;
  color: crimson;
}
.con button{
  border: 2px ridge gray;
  background: transparent;
  padding: 4px 15px;
  font-size: 18px;
  color: white;
}